import request from '../utils/request'

/**
 * 获取基础配置
 * @returns  基础配置
 */
export function getBasicConfigApi(){
    return request({
        url:"/config/getBasicConfig",
        method:"get"
    })
}
/**
 * 获取Theme配置
 * @returns  Theme配置
 */
export function getThemeConfigApi(){
    return request({
        url:"/config/getThemeConfig",
        method:"get"
    })
}

export function updateConfigApi(data){
    return request({
        url:"/config/updateConfig",
        method:"put",
        data
    })
}
