<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="基础配置" name="first">
      <el-form v-model="basicConfig" label-width="100px" style="width: 80%">
        <el-form-item label="网站名称">
          <el-input v-model="basicConfig.siteName"></el-input>
        </el-form-item>
        <el-form-item label="网站描述">
          <el-input type="textarea" v-model="basicConfig.siteDescription"></el-input>
          <p class="sava_p">支持html</p>
        </el-form-item>
        <el-form-item label="底部信息">
          <el-input type="textarea" v-model="basicConfig.footerInfo"></el-input>
          <p class="sava_p">支持html</p>
        </el-form-item>
        <el-form-item label="显示封面图">
          <el-switch v-model="basicConfig.showFeatureImage"></el-switch>
        </el-form-item>
        <el-form-item label="每页文章数">
          <el-slider v-model="basicConfig.postPageSize"></el-slider>
        </el-form-item>
        <el-form-item label="每页规档数">
          <el-slider v-model="basicConfig.archivesPageSize"></el-slider>
        </el-form-item>
        <el-form-item label="日期格式">
          <el-input v-model="basicConfig.dateFormat"></el-input>
          <p class="sava_p">dayjs</p>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="自定义配置" name="second">
      <el-tabs tab-position="left">
        <el-tab-pane label="布局">布局</el-tab-pane>
        <el-tab-pane label="颜色">颜色</el-tab-pane>
        <el-tab-pane label="鼠标">
          <el-radio-group v-model="topicConfig.mouse" style="margin: 40px">
            <el-radio label="none">无</el-radio>
            <el-radio label="peace">peace</el-radio>
            <el-radio label="love">love</el-radio>
            <el-radio label="fireworks">fireworks</el-radio>
          </el-radio-group>
        </el-tab-pane>
        <el-tab-pane label="背景">
          <el-card class="card_div">
            <div slot="header" class="clearfix">
              <span>网站背景图片</span>
            </div>
            <el-image :src="topicConfig.backgroundImage"
                      style="width: 200px"></el-image>
            <el-input v-model="topicConfig.backgroundImage"></el-input>
          </el-card>
          <el-card class="card_div">
            <div slot="header" class="clearfix">
              <span>头像背景图片</span>
            </div>
            <el-image :src="topicConfig.avatarBackGround"
                      style="width: 200px"></el-image>
            <el-input v-model="topicConfig.avatarBackGround"></el-input>
          </el-card>
        </el-tab-pane>
        <el-tab-pane label="目录">
          <div class="magic">
            <p>目录</p>
            <el-switch v-model="topicConfig.menu"></el-switch>
          </div>
        </el-tab-pane>
        <el-tab-pane label="赞赏">赞赏</el-tab-pane>
        <el-tab-pane label="社交">
          <el-button v-show="socials.length<=0" style="width: 100%" @click="addSocial"><i class="el-icon-plus"></i></el-button>
          <el-card class="box-card" v-for="(item,index) in socials"
                   :key="index">
            <div>
              <div class="social_input">
                <span class="social_span">名称:</span>
                <el-input v-model="item['socialName']" ></el-input>
              </div>
              <div class="social_input">
                <span class="social_span">图标:</span>
                <el-input v-model="item['socialIcon']"></el-input></div>
              <div class="social_input">
                <span class="social_span">链接:</span>
                <el-input v-model="item['socialLink']"></el-input>
              </div>
            </div>
            <el-button icon="el-icon-plus" circle size="small" @click="addSocial"></el-button>
            <el-button icon="el-icon-minus" circle size="small" @click="socials.remove(item)"></el-button>
          </el-card>
          <span class="social_intro">
            常用的社交图标，github：fa-github-alt，twitter：fa-twitter，微信：
            fa-wechat，QQ：fa-qq，图标采用的是font-awesome矢量字体图标，
            自定义图标可以从http://www.fontawesome.com.cn上查找
          </span>
        </el-tab-pane>
        <el-tab-pane label="友链">
          <el-button v-show="friends.length<=0" style="width: 100%" @click="addFriend">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-card class="box-card" v-for="(item,index) in friends"
                   :key="index">
            <div>
              <div class="social_input">
                <span class="social_span">名称:</span>
                <el-input v-model="item['siteName']" ></el-input>
              </div>
              <div class="social_input">
                <span class="social_span">头像:</span>
                <el-input v-model="item['siteAvatar']"></el-input></div>
              <div class="social_input">
                <span class="social_span">链接:</span>
                <el-input v-model="item['siteLink']"></el-input>
              </div>
              <div class="social_input">
                <span class="social_span">介绍:</span>
                <el-input v-model="item['siteIntro']"></el-input>
              </div>
            </div>
            <el-button icon="el-icon-plus" circle size="small" @click="addFriend"></el-button>
            <el-button icon="el-icon-minus" circle size="small" @click="friends.remove(item)"></el-button>
          </el-card>
        </el-tab-pane>
        <el-tab-pane label="魔改">
          <div class="magic">
            <p>粒子时钟</p>
            <el-switch v-model="topicConfig.particleClock"></el-switch>
          </div>
          <div class="magic">
            <p>博主介绍美化</p>
            <el-switch v-model="topicConfig.blogErJsPlus"></el-switch>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-tab-pane>
    <el-tab-pane label="网页图标" name="third">
      <el-image :src="basicConfig['favicon']"></el-image>
      <el-input v-model="basicConfig['favicon']"></el-input>
    </el-tab-pane>
    <el-tab-pane label="关于" name="fourth">
      <div class="columns" style="width: 100%;">
        <div class="column">
          <el-card class="box-card" shadow="never">
            <div id="preview"/>
          </el-card>
        </div>
      </div>
    </el-tab-pane>
    <el-button class="save_button" @click="saveConfig">保存</el-button>
  </el-tabs>
</template>

<script>
import {getThemeConfigApi} from "@/api/config"
import Vditor from "vditor";
import "vditor/dist/index.css"
import {updateConfigApi} from "../../../api/config";
Array.prototype.remove = function () {
  for (let i = 0; i < arguments.length; i++) {
    const ele = arguments[i];
    const index = this.indexOf(ele);
    if (index > -1) {
      this.splice(index, 1);
    }
  }
};
export default {
  name: "Theme",
  data() {
    return {
      contentEditor: null,
      activeName: 'second',
      basicConfig: {
        siteName: "",
        siteDescription: '',//网站描述
        footerInfo: '',//底部信息
        showFeatureImage: '',//显示封面图
        postPageSize: 0,//每页文章数
        archivesPageSize: 0,//每页归档数
        dateFormat: '',//日期格式
        favicon: '',//网站图标
        avatar: '',//头像
      },
      about: "",
      socials: [],
      friends:[],
      topicConfig: {
        mouse: 'peace',
        socials: [],
        friends:[],
        backgroundImage: '',
        avatarBackGround: '',
        particleClock:false,
        blogErJsPlus:false
      }
    }
  },
  created() {
    this.getBasicConfig()
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    getBasicConfig() {
      getThemeConfigApi().then(res => {
        //console.log(res)
        let {basicConfig, topicConfig, about} = res.data
        this.basicConfig = basicConfig
        this.about = about
        this.topicConfig = topicConfig
        this.socials=topicConfig.socials
        this.friends=topicConfig.friends
        this.renderMarkdown(about)
      })
    },
    renderMarkdown(md) {
      this.contentEditor = new Vditor('preview', {
        height: 500,
        placeholder: '此处为话题内容……',
        theme: 'classic',
        value: md,
        counter: {
          enable: true,
          type: 'markdown'
        },
        preview: {
          delay: 0,
          hljs: {
            style: this.topicConfig['previewHLJS'],
            lineNumber: false
          }
        },
        tab: '\t',
        typewriterMode: true,
        toolbarConfig: {
          pin: true
        },
        cache: {
          enable: false
        },
        mode: 'ir',
        upload: {
          // 上传字段名称
          fieldName: 'my-file',
          // 上传 url
          url: '',
          accept: 'image/*,.mp3, .wav, .rar'
        }
      })
    },
    saveConfig() {
      this.topicConfig.socials=this.socials
      this.topicConfig.friends=this.friends
      let data = {
        basicConfig: this.basicConfig,
        topicConfig: this.topicConfig,
        about: this.contentEditor.getValue()
      }
      updateConfigApi(data).then(() => {
        this.$message({
          message: "保存成功",
          type: "success"
        })
      })
    },
    addSocial() {
      this.socials.push({
        socialName: "",
        socialIcon: "fa-globe",
        socialLink: ""
      })
    },
    addFriend() {
      this.friends.push({
        socialName: "",
        socialAvatar: "",
        socialLink: ""
      })
    }
  },
}
</script>

<style scoped>
.sava_p {
  color: #c9c8c8;
}

.save_button {
  position: fixed;
  right: 50px;
  bottom: 20px;
}

.card_div {
  margin-bottom: 20px;
}
.social_input{
  display: flex;
  width: 60%;
  margin:0 auto 20px auto;
}
.social_span{
  width: 50px;
  height: 40px;
  line-height: 40px;
}
.social_intro{
  font-size: 14px;
  color: gray;
}
.magic{
  margin:40px;
}
</style>
